// Импорт цветов
@import "colors";
@import "fonts";

html {
	font-size: 9.89583333334px; }

body {
	width: 100%;
	margin: 0;
	background-color: $body-bg-color;
	position: relative; }
// MENU
.top-nav {
	width: 100%;
	height: 13rem;
	background-color: #fff;
	box-shadow: 0rem 0.4rem 2.5rem #000;
	z-index: 2;
	position: relative;
	.container-top-nav {
		height: 100%;
		padding: 0 16.2rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		a {
			text-decoration: none;
			color: $main-color; } } }

.menu {
	ul {
		list-style: none;
		li {
			display: inline;
			a {
				text-transform: uppercase;
				font-family: 'PTSans';
				font-weight: normal;
				font-style: normal;
				font-size: 1.4rem;
				margin-left: 10.6rem;
				position: relative;
				&.menu-items:after {
					content: '';
					display: inline-block;
					height: 1px;
					width: calc(100% + 0.6rem);
					background: #000;
					left: -0.3rem;
					bottom: -0.8rem;
					position: absolute;
					transform: scale(0);
					transition: all 0.65s; }
				&:hover {
					&:after {
						transform: scale(1); } } } } } }
.title {
	font-size: 7.2rem;
	font-family: 'Bahnschrift';
	display: flex;
	align-items: center;
	position: relative;
	.to {
		color: #000; }
	&:before {
		content: '';
		display: inline-block;
		position: absolute;
		left: -2.2rem;
		height: 6.8rem;
		width: 1px;
		background: #000;
		transform: scale(0);
		transition: all 0.65s; }
	&:hover {
		&:before {
			transform: scale(1); } } }


// Remove inaccessible links
.account {
	display: none; }
// Footer
footer {
	background-color: #fff;
	width: 100%;
	height: 23.3rem;
	box-shadow: 0px -6px 10px rgba(0, 0, 0, 0.25);
	display: flex;
	position: relative;
	z-index: 10;

	font-family: 'PTSans';
	font-style: normal;
	font-weight: normal;
	font-size: 3rem;
	line-height: 3.9rem; }
.footer-section {
	width: 64rem;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	&:after {
		content: '';
		width: 1px;
		height: 16.5rem;
		background-color: #000;
		display: inline;
		position: absolute;
		right: 0; }
	&:last-child {
		&:after {
			content: none; } } }
.contacts-title {
	font-weight: bold;
	padding: 0;
	margin: 0; }
.contacts-footer-container {
	margin-top: 3.2rem;
	& p {
		padding: 0;
		margin: 0;
		& span {
			font-family: 'FugazOne';
			line-height: 4.4rem; } } }
.social-footer {
	& p {
		margin: 0;
		margin-bottom: 1.7rem; }
	& a {
		&:after {
			content: '';
			display: block;
			height: 1px;
			width: 0%;
			margin-top: 1rem;
			background-color: #000;
			transition: all 0.65s; }
		&:hover:after {
			width: 100%; } } }


.social-links {
	font-family: 'FugazOne';
	line-height: 4.4rem; }

.menu-footer {
	// height: 16.5rem
	display: flex;
	flex-direction: column;
	height: 100%;
	& ul {
		margin: 0;
		padding: 0;
		height: 100%;
		list-style: none;
		padding: 1rem 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		& li {
			display: block;
			& a {
				text-decoration: none;
				color: inherit;
				float: left;
				&:after {
					content: '';
					display: block;
					height: 1px;
					width: 0%;
					background-color: #000;
					transition: all 0.65s; }
				&:hover:after {
					width: 100%; } } } } }


// Main
.container-contacts {
	display: flex;
	justify-content: center;
	width: 100%; }
.contacts {
	width: 181rem;
	height: 67.2rem;
	background-color: $block-bg-color;
	position: relative;
	box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25); }
